.vote-container {
  padding: 2.083vw 0;

  .hide-lg {
    display: none;
  }

  .purple-right-arrow {
    background: url(../../images/v2/vote/purple-right-arrow.svg) no-repeat center 0.2rem;
    content: '';
    width: 14px;
    height: 1rem;
    display: inline-block;
    margin-left: 6px;
  }

  .common-flex {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 16px;

    .common-title {
      // font-size: 1.111vw;
      font-size: 14px;
      font-weight: 500;
      color: #fff;
      display: flex;
      align-items: center;

      &::before {
        content: '';
        width: 6px;
        height: 1em;
        display: inline-block;
        background: #4d54ff;
        border-radius: 1px;
        margin-right: 10px;
      }
    }

    .common-desc {
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      color: rgba(255, 255, 255, 0.8);
    }
  }

  .step-container {
    display: flex;
    gap: 30px;
    margin: 20px 0 30px;

    .left-steps {
      display: flex;
      // flex: 2 1 auto;
      flex: 0 0 66%;

      padding: 17px 30px;
      background: #282931;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
      border-radius: 2px;
      display: flex;
      flex-direction: row;
      gap: 10px;

      .step {
        display: flex;
        gap: 10px;
        align-items: center;
      }

      .step-number {
        border-radius: 50%;
        background: rgba(255, 255, 255, 0.04);
        // background: red;
        height: 30px;
        width: 30px;
        // text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #9195fb;
      }

      .step-text-box {
        display: flex;
        flex-direction: column;

        .step-title {
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 20px;
          color: #ffffff;
        }

        .step-desc {
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 19px;
          color: rgba(255, 255, 255, 0.6);
        }
      }

      .step-arrow {
        background: url(../../images/v2/vote/step-arrow.svg) no-repeat;
        width: 50px;
        height: 5px;
      }
    }

    .right-forum {
      padding: 17px 30px;
      flex: auto;
      background: #282931 url(../../images/v2/vote/right-forum-card.svg) no-repeat center right 20px;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
      border-radius: 2px;

      .forum-title {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        color: #ffffff;
        margin-bottom: 4px;
      }

      .forum-desc {
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        color: #9195fb;

        &:hover {
          border-bottom: 1px solid #9195fb;
          padding-bottom: 1px;
          // text-decoration: underline;
        }
      }
    }
  }

  .body-container {
    display: flex;
    gap: 30px;
  }

  .proposal-container {
    flex: 0 0 66%;
    background: #282931;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
    border-radius: 2px;
    padding-bottom: 16px;

    .proposal-header {
      padding: 28px 30px 0px;

      display: flex;
      justify-content: space-between;
      align-items: center;

      .proposal-left {
        display: flex;
        gap: 8px;

        .proposal-title {
          font-style: normal;
          font-weight: 600;
          font-size: 20px;
          line-height: 28px;
          color: #ffffff;
        }

        .proposal-count {
          background: rgba(76, 84, 255, 0.3);
          border-radius: 1px;
          font-weight: 600;
          font-size: 12px;
          line-height: 16px;
          text-transform: capitalize;

          color: #ffffff;
          align-self: center;
          padding: 5px;
        }
      }

      .proposal-right {
        display: flex;
        gap: 6px;
        line-height: 22px;
        padding: 6px 16px;
        background: #22232b;
        border-radius: 2px;

        .new-proposal {
          font-style: normal;
          font-weight: 400;
          font-size: 12px;
          line-height: 20px;
          color: #ffffff;
        }

        .bell {
          background: url(../../images/v2/vote/white-bell.svg) no-repeat center;
          width: 14px;
        }

        .new-proposal-txid {
          font-weight: 500;
          font-size: 12px;
          line-height: 20px;
          color: #9195fb;

          &:hover {
            // border-bottom: 1px solid #9195FB;
            // padding-bottom: 1px;
            text-decoration: underline;
          }
        }
      }
    }

    .active-proposal {
      background: #22232b;
      border-radius: 2px;
      padding: 40px 20px;
      margin: 0px 30px;
      position: relative;

      .corner-tag {
        position: absolute;
        top: 0;
        left: 0;
        background: rgba(143, 247, 226, 0.16);
        font-weight: 500;
        font-size: 12px;
        line-height: 17px;
        text-transform: capitalize;
        color: rgba(143, 247, 226, 0.8);
        padding: 2px 12px;
      }

      .p-body {
        display: flex;
        // padding: 42px 20px;
        justify-content: space-between;
        background: #22232b;
        border-radius: 2px;
        flex-direction: column;
        gap: 20px;
      }

      .p-first-row {
        display: flex;
        gap: 10px;
        justify-content: space-between;

        .p-title {
          font-weight: 600;
          font-size: 20px;
          line-height: 28px;
          color: #9195fb;
        }

        .vote-btn {
          flex-shrink: 0;
          background: #4c54ff;
          border-radius: 2px;
          padding: 6px 26px;
          font-weight: 500;
          font-size: 14px;
          line-height: 20px;
          text-align: center;
          color: #ffffff;
          height: fit-content;
        }
      }

      .p-second-row {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .p-desc {
          display: flex;
          align-items: center;
          width: fit-content;
        }

        .p-id {
          background: rgba(255, 255, 255, 0.08);
          border-radius: 2px;
          font-style: normal;
          font-weight: 500;
          font-size: 12px;
          line-height: 16px;

          color: #ffffff;
          padding: 4px 10px;
        }

        .p-countdown-text {
          font-weight: 500;
          font-size: 14px;
          line-height: 20px;
          color: rgba(255, 255, 255, 0.6);
          margin-left: 10px;
        }

        .p-countdown-date {
          font-weight: 500;
          font-size: 14px;
          line-height: 19px;

          color: #ffffff;
          margin-left: 6px;
        }

        .p-vote-bar {
          display: flex;
          align-items: center;

          .p-for {
            font-style: normal;
            font-weight: 600;
            font-size: 12px;
            line-height: 16px;
            text-align: right;

            color: #8ff7e2;
          }

          .p-for-bar {
            background: #8ff7e2;
            mix-blend-mode: normal;
            transform: matrix(-1, 0, 0, 1, 0, 0);
            width: 20px;
            height: 6px;
            margin-left: 6px;

            &::after {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              border-bottom: 6px solid #22232b;
              border-right: 6px solid transparent;
              width: 0;
            }
          }

          .p-against-bar {
            background: #ff5266;
            mix-blend-mode: normal;
            transform: matrix(1, 0, 0, -1, 0, 0);
            width: 20px;
            height: 6px;
            margin-right: 6px;

            &::before {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              border-bottom: 6px solid #22232b;
              border-right: 6px solid transparent;
              width: 0;
            }
          }

          .p-against {
            font-weight: 600;
            font-size: 12px;
            line-height: 16px;
            text-align: right;

            color: #ff5266;
          }
        }
      }
    }

    .no-new-proposal {
      background: #22232b url(../../images/v2/vote/dark-no-new-proposal.svg) no-repeat right 30px;
      border-radius: 2px;

      display: flex;
      flex-direction: column;
      gap: 16px;
      padding: 40px 30px;
      margin: 0px 30px;

      .proposal-title {
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        color: #ffffff;
      }

      .proposal-desc {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        color: #9195fb;
        width: fit-content;

        &:hover {
          border-bottom: 1px solid #9195fb;
          padding-bottom: 1px;
          // text-decoration: underline;
        }
      }
    }

    .past-proposal-list {
      display: flex;
      flex-direction: column;

      .p-item {
        padding: 20px 30px;
        gap: 20px;
        display: flex;
        flex-direction: column;

        &:hover {
          background: rgba(255, 255, 255, 0.04);
        }

        &:hover > .first-row .arrow-right {
          opacity: 1;
          margin-left: 10px;
        }

        .first-row {
          display: flex;
          justify-content: space-between;
          gap: 20px;

          .p-title {
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            color: #ffffff;
          }

          .arrow-right {
            background: url(../../images/v2/arrow-right.svg) no-repeat center center;
            height: 1em;
            width: 1em;
            display: inline-block;
            opacity: 0;
            transition: 0.3s;
          }

          .p-green-tick {
            flex-shrink: 0;
            margin-right: 35px;
            background: url(../../images/v2/vote/green-tick.svg) no-repeat center center;
            height: 20px;
            width: 20px;
          }

          .p-red-exclamation {
            flex-shrink: 0;
            margin-right: 35px;
            background: url(../../images/v2/vote/dark-red-exclamation.svg) no-repeat center center;
            height: 20px;
            width: 20px;
          }
        }

        .second-row {
          display: flex;
          align-items: center;
          justify-content: space-between;

          .p-left {
            display: flex;
            align-items: center;
            gap: 10px;

            .p-id {
              font-weight: 500;
              font-size: 12px;
              line-height: 16px;

              color: rgba(255, 255, 255, 0.6);
              background: rgba(255, 255, 255, 0.04);
              border-radius: 2px;
              padding: 4px 10px;
            }

            .p-date {
              font-weight: 500;
              font-size: 14px;
              line-height: 19px;

              color: rgba(255, 255, 255, 0.6);
            }
          }

          .p-status {
            font-weight: 500;
            font-size: 12px;
            line-height: 17px;
            text-transform: capitalize;

            color: #ffffff;
            background: rgba(255, 255, 255, 0.04);
            border-radius: 2px;
            padding: 4px 28px;
          }
        }
      }
    }

    .load-more {
      cursor: pointer;
      width: fit-content;
      font-weight: 500;
      font-size: 12px;
      line-height: 16px;
      margin: auto;
      color: rgba(255, 255, 255, 0.6);
      display: flex;
      justify-content: center;

      &::after {
        content: '';
        display: flex;
        background: url(../../images/v2/arrow-white.svg) no-repeat center center;
        height: 1rem;
        width: 1rem;
        opacity: 0.6;
      }
    }
  }

  .right-panel {
    flex: auto;
    display: flex;
    flex-direction: column;
    gap: 20px;

    .my-vote-container {
      // flex: auto;
      display: flex;
      flex-direction: column;
      align-items: center;
      // height: 100px;
      background: #282931;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
      border-radius: 2px;

      .my-vote-header {
        padding: 30px 30px 0px;
        align-self: start;
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 28px;
        text-transform: capitalize;
        color: #ffffff;
      }

      .unconnected-wallet-logo {
        display: flex;
        background: url(../../images/v2/vote/unconnected-wallet.svg) no-repeat center center;
        height: 80px;
        width: 80px;
      }

      .my-vote-text {
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;

        color: #ffffff;
        padding: 18px 0 40px;
      }

      .tooltip-icon {
        display: flex;
        background: url(../../images/v2/vote/white-qustion-mark-tooltip.svg) no-repeat center center;
        height: 14px;
        width: 14px;
      }

      .total-votes {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        /* identical to box height, or 143% */

        text-align: center;

        color: rgba(255, 255, 255, 0.6);
        display: flex;
        align-items: center;
        gap: 6px;
      }

      .total-votes-amount {
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 27px;
        text-transform: capitalize;

        color: #ffffff;
        margin-top: 8px;
      }

      .vote-info-box {
        background: #22232b;
        border-radius: 2px;
        display: flex;
        flex-direction: column;
        width: calc(100% - 60px);
        padding: 20px 16px;
        gap: 16px;
        margin-top: 26px;
        margin-bottom: 20px;

        .vote-info-row {
          display: flex;
          justify-content: space-between;

          .vote-info-text {
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 20px;
            color: rgba(255, 255, 255, 0.6);
          }

          .vote-info-amount {
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 20px;
            text-align: right;

            color: #ffffff;
          }
        }
      }

      .my-vote-btn-wrapper2 {
        padding: 0 30px 26px;
        width: 100%;
        display: flex;
        gap: 21px;
      }

      .my-vote-btn-wrapper {
        padding: 0 30px 26px;
        width: 100%;
      }

      .my-vote-btn {
        cursor: pointer;
        background: #4c54ff;
        border-radius: 2px;

        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        color: #ffffff;
        padding: 10px;
        width: 100%;
      }

      .my-vote-btn2 {
        cursor: pointer;
        background: rgba(255, 255, 255, 0.04);
        border-radius: 2px;
        color: #9195fb;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        padding: 10px;
        width: 100%;
      }

      .tick-vote-logo {
        background: url(../../images/v2/vote/tick-vote.svg) no-repeat center center;
        height: 80px;
        width: 80px;
      }
    }

    .faq-container {
      display: flex;
      flex-direction: column;
      background: #282931;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
      border-radius: 2px;

      .faq-header {
        padding: 20px 30px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .faq-title {
          font-weight: 600;
          font-size: 16px;
          line-height: 22px;
          text-transform: capitalize;

          color: #ffffff;
        }

        .faq-more {
          font-weight: 500;
          font-size: 12px;
          line-height: 16px;
          text-align: right;
          display: flex;
          color: rgba(255, 255, 255, 1);
          opacity: 0.6;

          &::after {
            content: '';
            background: url(../../images/v2/vote/white-right-arrow.svg) no-repeat center center;
            height: 1em;
            width: 1em;
          }

          &:hover {
            opacity: 1;
          }
        }
      }

      .faq-body {
        padding: 0 30px 20px;

        ul {
          display: flex;
          flex-direction: column;
          gap: 16px;
          margin: 0 !important;
        }

        li {
          &::before {
            content: '\2022';
            color: #ffffff;
            font-weight: bold;
            display: inline-block;
            width: 1em;
            margin-left: -1em;
          }
        }

        a {
          font-weight: 400;
          font-size: 14px;
          line-height: 20px;
          color: rgba(255, 255, 255, 0.6);

          &:hover {
            font-weight: 500;
            color: #ffffff;
          }
        }
      }
    }
  }

  .ant-divider-inner-text {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    text-transform: capitalize;

    color: rgba(255, 255, 255, 0.6);
    padding: 10px;
  }

  .ant-divider {
    margin: 20px 0;
    border-top-color: #ffffff;
    opacity: 0.06;
  }

  .mobile-menu {
    display: none;
    justify-content: space-evenly;

    padding-bottom: 8.5px;
    padding-top: 20px;

    border-bottom: 1px solid rgba(255, 255, 255, 0.06);

    .menu-item {
      position: relative;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      text-transform: capitalize;
      cursor: pointer;
      color: rgba(255, 255, 255, 0.4);

      .p-left {
        display: flex;
        gap: 4px;
      }

      .p-title {
        font-size: 16px;
        line-height: 22px;
        text-transform: capitalize;
        // color: #FFFFFF;
      }

      .p-count {
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        line-height: 16px;

        color: #9195fb;

        background: rgba(76, 84, 255, 0.3);
        border-radius: 1px;
        padding: 2px;
        display: flex;
        align-items: center;
      }

      &::after {
        content: '';
        display: block;
        width: 0;
        height: 2px;
        position: absolute;
        bottom: -10px;
        left: 50%;
        transform: translateX(-50%);
        background: #4c54ff;
        border-radius: 1px;
        // transition: width 0.3s ease-in-out;
      }

      &:hover,
      &.current {
        color: #ffffff;
        font-weight: 600;

        .p-count {
          color: #ffffff;
          background: #4c54ff;
        }

        &::after {
          width: 100%;
          min-width: 40px;
        }
      }

      &:last-child {
        margin-right: 0;
      }
    }
  }
}

.vote-modal > .ant-modal-content {
  .ant-modal-close-x {
    color: #fff;
    transform: scale(1.2);
    // padding-top: 3px;
  }

  .ant-divider {
    opacity: 1;
    border-top-color: rgba(255, 255, 255, 0.06);
  }

  .ant-modal-body {
    position: relative;
    display: flex;
    background: #282931;
    border-radius: 2px;
    // justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 0;
    min-height: 574px;

    .v-title {
      font-style: normal;
      font-weight: 600;
      font-size: 16px;
      line-height: 22px;
      text-align: center;

      color: #ffffff;
      padding-top: 20px;
    }

    .tick-vote-logo {
      margin-top: 20px;
      margin-bottom: 20px;
      background: url(../../images/v2/vote/tick-vote.svg) no-repeat center center;
      height: 80px;
      width: 80px;
    }

    .vote-authorize-logo {
      height: 80px;
      width: 80px;
      background: url(../../images/voteAuthorize.svg) no-repeat center center;
      // margin: 0 auto;
      margin-top: 20px;
      margin-bottom: 20px;
    }

    .v-desc {
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      text-align: center;
      color: #ffffff;

      .bold {
        font-weight: 600 !important;
      }
    }

    .v-desc2 {
      display: flex;
      justify-content: space-between;
      width: 100%;
      padding: 50px 20px 10px;

      .v-amount {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: rgba(255, 255, 255, 0.6);
      }

      .v-right {
        display: flex;
        gap: 6px;

        .v-text {
          font-weight: 400;
          font-size: 12px;
          line-height: 20px;
          text-align: right;
          color: rgba(255, 255, 255, 0.6);
        }

        .v-balance {
          font-style: normal;
          font-weight: 500;
          font-size: 12px;
          line-height: 20px;
          text-align: right;

          color: #ffffff;
        }

        .v-get-btn {
          font-style: normal;
          font-weight: 400;
          font-size: 12px;
          line-height: 20px;
          text-align: right;

          color: #9195fb;

          &:hover {
            // border-bottom: 1px solid #9195FB;
            // padding-bottom: 1px;
            text-decoration: underline;
          }
        }

        .v-arrow {
          background: url(../../images/v2/vote/purple-right-arrow2.svg) no-repeat center center;
          align-self: center;
          height: 5px;
          width: 3px;
        }
      }
    }

    .vote-input {
      // padding: 0 20px;
      width: calc(100% - 40px);
      border-radius: 2px;
      height: 60px;
      background: #393a41;
      border: none;

      &.invalid {
        border: 1px solid #ff5266;
        background: #392c36 !important;
        border-radius: 2px;
      }

      &.invalid > .ant-input {
        background: #392c36 !important;
        color: #ff5266;
      }

      .ant-input {
        background: #393a41;
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;

        color: rgba(255, 255, 255, 0.4);

        &:not([value='']) {
          color: #ffffff;
        }
      }

      .ant-input-suffix {
        cursor: pointer;
        color: #ffffff;
        padding-left: 16px;
        border-left: solid 1px rgba(255, 255, 255, 0.1);
        height: 36px;
        align-self: center;
        position: relative;

        .ant-input-clear-icon {
          position: absolute;
          left: -30px;

          background: url(../../images/v2/clear-icon.svg) no-repeat center center;
          transform: scale(1.5);

          svg[data-icon='close-circle'] {
            opacity: 0;
          }
        }
      }
    }

    .exceed-remain-votes {
      margin-top: 6px;
      padding: 10px 16px;
      font-weight: 400;
      font-size: 12px;
      line-height: 20px;
      color: #ff5266;
      display: flex;
      width: calc(100% - 40px);
      align-items: center;
      gap: 6px;

      &::before {
        content: '';
        width: 12px;
        height: 12px;
        background: url(../../images/v2/vote/warning.svg) no-repeat center center;
      }
    }

    .modal-btn {
      width: calc(100% - 40px);
      display: flex;
      justify-content: center;
      align-items: center;
      background: #53545a;
      border-radius: 2px;
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      /* identical to box height, or 143% */
      position: absolute;
      bottom: 40px;

      text-align: center;

      color: rgba(255, 255, 255, 0.6);
      cursor: not-allowed;

      &.is-valid {
        cursor: pointer;
        background: #4c54ff;
        color: #ffffff;
      }
    }
  }
}

