@media screen and (max-width: 1000px) {
  .vote-container {
    padding: 2.083vw 15px;

    &.white {
      .body-container {
        .proposal-container {
          .proposal-right {
            background: #ffffff;
          }
        }
      }
    }

    .mobile-menu {
      width: 100%;
      display: flex;
    }

    .hide-md {
      position: absolute;
      display: none;
    }

    .hide-lg {
      display: inline;
    }

    .common-flex {
      flex-direction: column;
      align-items: start;

      &::before {
        width: 4px;
      }
    }

    .step-container {
      flex-direction: column;
      gap: 10px;

      .left-steps {
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 20px;
        padding: 20px 15px;

        .step {
          width: fit-content;

          .step-text-box {
            .step-title {
              font-weight: 400;
            }

            .step-desc {
              font-size: 12px;
              line-height: 16px;
            }
          }
        }

        .step-arrow {
          display: none;
        }
      }

      .right-forum {
        padding: 16px 22px;

        .forum-desc {
          font-weight: 400;
          font-size: 12px;
        }
      }
    }

    .body-container {
      .proposal-container {
        flex: 0 0 100%;

        .proposal-header {
          padding-bottom: 30px;

          .proposal-left {
            display: none;
          }

          .proposal-right {
            padding-left: 0;
            // background: #FFFFFF;
          }
        }

        .active-proposal {
          .corner-tag {
            font-weight: 400;
            right: 0;
            left: auto;
          }

          .p-body {
            gap: 16px;

            .p-first-row {
              .p-title {
                font-size: 16px;
                line-height: 22px;
              }
            }

            .p-second-row {
              // justify-items: start;
              align-items: flex-start;
              flex-direction: column;
              gap: 26px;

              .p-countdown-text {
                font-weight: 400;
                font-size: 12px;
                line-height: 17px;
              }

              .p-countdown-date {
                font-size: 12px;
                line-height: 16px;
              }

              .p-right-mobile {
                flex-wrap: wrap;
                // gap: 10px;

                .p-vote-bar {
                  flex-direction: column;

                  .bar-first-row {
                    width: 100%;
                    justify-content: space-between;
                    padding: 0 6px;
                    display: flex;
                  }

                  .bar-second-row {
                    display: flex;
                    flex-direction: row;
                  }
                }

                .vote-btn {
                  flex-shrink: 0;
                  background: #4c54ff;
                  border-radius: 2px;
                  padding: 3px 20px;
                  color: #ffffff;
                }

                display: flex;
                width: 100%;
                justify-content: space-between;
              }
            }
          }
        }

        .past-proposal-list {
          .p-item {
            .first-row {
              .p-title {
                font-size: 14px;
                line-height: 20px;
              }
            }

            .second-row {
              .p-date {
                font-size: 12px;
                line-height: 16px;
              }

              .p-status {
                font-weight: 400;
              }
            }
          }
        }
      }

      .right-panel {
        .my-vote-container {
          .space-60 {
            height: 60px;
          }

          .ant-divider {
            display: none;
          }

          .my-vote-header {
            display: none;
          }
        }
      }
    }
  }
}
